import React from "react";
import { Link } from "react-router-dom";
import logo from "./images/logo__/ms-icon-144x144.png";
import "./Header.css";

export default function Header(props) {
  // console.log(bg);
  return (
    <header id='header' style={{backgroundColor:props.bg}}>
      <Link to="/">
        <img className="logo_" src={logo} alt="" />
      </Link>
      <h3>A R T F L Y</h3>
      <pre className='hpre'>Our gifts your choice</pre>
    </header>
  );
}
