const menufunc = () => {
  let menubar = document.getElementById("menubar");
  //   let body = document.getElementById("body");

  if (menubar.style.left > "-60vw") {
    menubar.style.left = "-60vw";
    // body.style.opacity = 1;
  } else {
    menubar.style.left = "0";
    // body.style.opacity = 0.8;
  }
};
