const display2 = () => {
  let menubar = document.getElementById("menubar");
  //   let body = document.getElementById("body");

  if (menubar.style.left > "-60vw") {
    menubar.style.left = "-60vw";
    //   body.style.opacity = 1;
  }
};
const backfunc = (id1, id2, id3) => {
  let sec = document.getElementById(id1);
  let back = document.getElementById(id2);
  let next = document.getElementById(id3);
  // console.log(sec);

  //   if (sec.style.marginLeft === '2.5vw') {
  //       sec.style.marginLeft = '-90vw';
  //       back.style.display = 'block';
  //       next.style.display = 'none';
  //   }
  next.style.display = "block";

  if (sec.style.marginLeft === "-90vw") {
    sec.style.marginLeft = "2.5vw";
    back.style.display = "none";
  } else if (sec.style.marginLeft === "-184vw") {
    sec.style.marginLeft = "-90vw";
  } else if (sec.style.marginLeft === "-262vw") {
    sec.style.marginLeft = "-184vw";
  } else {
    sec.style.marginLeft = "-262vw";
  }
};
const nextfunc = (id1, id2, id3) => {
  let sec = document.getElementById(id1);
  let back = document.getElementById(id2);
  let next = document.getElementById(id3);
  // console.log(sec);

  // if (sec.style.marginLeft === '-90vw') {
  //     sec.style.marginLeft = '2.5vw';
  //     back.style.display = 'none';
  //     next.style.display = 'block';
  // }
  back.style.display = "block";

  if (sec.style.marginLeft === "-262vw") {
    sec.style.marginLeft = "-298vw";
    next.style.display = "none";
  } else if (sec.style.marginLeft === "-184vw") {
    sec.style.marginLeft = "-262vw";
  } else if (sec.style.marginLeft === "-90vw") {
    sec.style.marginLeft = "-184vw";
  } else {
    sec.style.marginLeft = "-90vw";
  }
};
