export default function Product_Item_page(props) {
  let history = useHistory();
  if (props.images === 2) {
    return (
      <div className="main_">
        <p className="goback_" onClick={() => history.goBack()}>
          {"< Back"}
        </p>
        <div className="preview">
          <span className="left">
            <Link to="/">
              <img id="img1" src={props.img1} alt="" />
              <img id="img2" src={props.img2} alt="" />
            </Link>
          </span>
        </div>
        <div className="preview_right">
          <span className="right2">
            <img src={props.img1} alt="" id="_1" onClick={() => click1()} />
            <img src={props.img2} alt="" id="_2" onClick={() => click2()} />
          </span>
        </div>
        <div className="desc_">
          <h1>{props.title}</h1>
          <p className="rs">{props.rs}</p>
          <br />
          <br />
          <p>
            {props.desc_l1}
            <br />
            {props.desc_l2}
            <br />
            {props.desc_l3}
            <br />
            {props.desc_l4}
            <br />
            {props.desc_l5}
          </p>
        </div>

        <div className="buttons">
          <Link to="/buynow_page">
            <button className="buy-btn">Buy Now</button>
          </Link>
        </div>
      </div>
    );
  } else if (props.images === 5) {
    return (
      <div className="main_">
        <p className="goback_" onClick={() => history.goBack()}>
          {"< Back"}
        </p>
        <div className="preview">
          <span className="left">
            <a href="fc-7.html">
              <img id="img1_" src={props.img1} alt="" />
              <img id="img2_" src={props.img2} alt="" />
              <img id="img3_" src={props.img3} alt="" />
              <img id="img4_" src={props.img4} alt="" />
              <img id="img5_" src={props.img5} alt="" />
            </a>
          </span>
        </div>
        <div className="preview_right">
          <span className="right2">
            <img src={props.img1} alt="" id="_1_" onClick={() => click1_()} />
            <img src={props.img2} alt="" id="_2_" onClick={() => click2_()} />
            <img src={props.img3} alt="" id="_3_" onClick={() => click3_()} />
            <img src={props.img4} alt="" id="_4_" onClick={() => click4_()} />
            <img src={props.img5} alt="" id="_5_" onClick={() => click5_()} />
          </span>
        </div>
        <div className="desc_">
          <h1>{props.title}</h1>
          <p className="rs">{props.rs}</p>
          <br />
          <br />
          <p>
            {props.desc_l1}
            <br />
            {props.desc_l2}
            <br />
            {props.desc_l3}
            <br />
            {props.desc_l4}
            <br />
            {props.desc_l5}
          </p>
        </div>

        <div className="buttons_">
          <a href="Buy-now.html">
            <button className="buy-btn">Buy Now</button>
          </a>
        </div>
      </div>
    );
  } else if (props.images === 1) {
    return (
      <div className="main_" style={{marginBottom:'2vh'}}>
        <p className="goback_" onClick={() => history.goBack()}>
          {"< Back"}
        </p>
        <div className="preview">
          <span className="left">
            <a href="fc-33.html">
              <img id="img1__" src={props.img1} alt="" />
              {/* <img id="img2" src="images/read_more/canavas_sec/6/1.jpeg" alt=""> */}
              {/* {/* <img id="img3" src="images/read_more/frame_sec/16/3.jpeg" alt=""> */}
            </a>
          </span>
        </div>
        {/* <div className="preview_right">
                    <span className="right2">
                        <img src="images/read_more/pencile_sec/1.jpg" alt="" id="_1">
                        <img src="images/read_more/frame_sec/16/2.jpg" alt="" id="_2" onclick="click2()">
                        <img src="images/read_more/frame_sec/16/3.jpeg" alt="" id="_3" onclick="click3()">
                    </span>
                </div> */}
        <div className="desc_">
          <h1>{props.title}</h1>
          <p className="rs">{props.rs}</p>
          <br />
          <br />
          <p>
            {props.desc_l1}
            <br />
            {props.desc_l2}
            <br />
            {props.desc_l3}
            <br />
            {props.desc_l4}
            <br />
            {props.desc_l5}
          </p>
        </div>

        <div className="buttons" style={{ marginTop: "-4vh" }}>
          <a href="Buy-now.html">
            <button className="buy-btn">Buy Now</button>
          </a>
        </div>
      </div>
    );
  }
}
