import React, { useState } from "react";
import "./Buy_now.css";

document.title = "Buy Now Page";
function send_handle() {
  let specialNote = document.getElementById("note").value;

  let landMark = document.getElementById("landMark").value;

  let pincode = document.getElementById("pincode").value;

  let address = document.getElementById("address").value;

  let phone = document.getElementById("phone").value;

  let email = document.getElementById("email").value;

  let name = document.getElementById("name").value;
  if (!name) {
    alert("Please fill Your name!");
  } else if (!phone) {
    alert("Please fill Your Phone Number!");
  } else if (phone.length !== 10) {
    alert("Please fill your phone no. correctly!");
  } else if (!email) {
    alert("Please fill Your Email!");
  } else if (!address) {
    alert("Please fill Your Address!");
  } else if (!pincode) {
    alert("Please fill Your Area Pincode!");
  } else if (pincode.length !== 6) {
    alert("Your Postal code is Invalid!");
  } else if (!landMark) {
    alert("Please fill out Your land mark!");
  } else if (
    name &&
    email &&
    phone &&
    phone.length === 10 &&
    address &&
    pincode &&
    pincode.length === 6 &&
    landMark
  ) {
    var win = window.open(
      `https://wa.me/917755832567?text=Name:%20${name}%0AEmail:%20${email}%0AContact%20No:%20${phone}%0AAddress:%20${address}%0APincode:%20${pincode}%0ALand%20Mark:%20${landMark}%0ASpecial%20Note:%20${specialNote}`,
      "_blank"
    );
  }
  // win.focus();
}
